/* https://grid.layoutit.com/ */
.grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  gap: 5px 5px;
  position: absolute;
  /* TODO: how to get it to just fill all available vertical space? 
     Without taking _more_ than is available, thereby covering other elements? */
  height: 85%;
  width: 99%;
}

.cell {
  background-color: rgb(44, 160, 26);
}
.cell:nth-child(2n) {
  background-color: rgb(0, 30, 255);
}
